<template lang="pug">
section.about
  .left-side
    h1 About
    h2 
      |
      br
      |  
    .vd
      .cover(
        @click="mainPlayer.playVideo()"
        :class="showVdCover ? 'on' : ''"
      )
      #player
    p
      |19675018,219
      br
      br
      |50

  .right-side
    section.features
      .feature
        .icon.i0
        h1 
        .des
          span.number 40
          |
          br
          span.number 50
          |
        .photo
          img(src="../../../assets/about_photo0.jpg")
      .feature
        .icon.i1
        h1 
        .des
          |
          br
          |
          span.number 7000
          |
          br
          |
          span.number 45,000
          |
        .photo
          img(src="../../../assets/about_photo1.jpg")
      .feature
        .icon.i2
        h1 
        .des
          | 
          br
          span.number 200
          |
        .photo
          img(src="../../../assets/about_photo2.jpg")
      .feature
        .icon.i3
        h1 
        .des
          | ERP 
          br
          |
        .photo
          img(src="../../../assets/about_photo3.jpg")
      
    
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import $ from 'jquery';

export default {
  name: 'About',
  data() {
    return {
      mainPlayer: null,
      showVdCover: true,
      alreadyStart: false,
    };
  },
  computed: {
    ...mapState({
      loadingShow: state => state.loadingShow,
    }),
  },
  mounted() {
    $(window).on('load', this.creatVideo);
  },
  methods: {
    ...mapMutations(['changeStateKeyValue']),
    creatVideo() {
      this.mainPlayer = new YT.Player(
        'player',
        {
          height: '100%',
          width: '100%',
          videoId: 'idKh-rc0Zz8',
          playerVars: {
            controls: 0,
            autoplay: false,
            enablejsapi: 0,
            rel: 0,
            showinfo: 0,
            modestbranding: 1,
            cc_load_policy: 1,
            playsinline: 1,
          },
          events: {
            onReady: () => {
              this.mainPlayer.pauseVideo();
            },
            onStateChange: this.vdStateChange,
          },
        },
      );
    },
    vdStateChange(event) {
      if (event.data !== 1) this.showVdCover = true;
      else this.showVdCover = false;
    },
  },
};
</script>

<style lang="scss" scoped>
//desktop
$vpw:1920;
$vph:1080;
.about{
  @include r;
  box-sizing: border-box;
  padding: getVW(70);

  .left-side{
    @include alt;
    left: getVW(70);
    width: getVW(300);
    top: getVW(70);
    height: calc(100% - #{getVW(70)});
    box-sizing: border-box;
    padding-left: getVW(30);
    z-index: 1;

    h1{
      @include r;
      @include fontvw(42,42,#006f37,bolder);
      font-family: 'Arial';
      margin-bottom: getVW(40);
    }

    h2{
      @include r;
      @include fontvw(20,38,#5c5c5c);
      margin-bottom: getVW(20);
    }

    .vd{
      @include altvw(674,379,100,0,84);
      left: 100%;

      .cover{
        @include alt;
        z-index: 9;
        pointer-events: none;
        opacity: 0;
        background-image: url(../../../assets/btn_videoplay.png);
        background-repeat: no-repeat;
        background-position: center center;
        cursor: pointer;

        &.on{
          opacity: 1;
          pointer-events: auto;
        }
      }

      #player{
        z-index: 0;
      }
    }

    p{
      @include r;
      @include fontvw(14,24,#5c5c5c);
      text-align: justify;
    }
  }

  .right-side{
    @include r;
    box-sizing: border-box;
    padding-left: getVW(384);
    z-index: 0;

    .features{
      @include r;
      margin-top: getVW(440);

      .feature{
        @include r(49%);
        margin-right: 2%;
        margin-bottom: getVW(55);

        &:nth-child(2n){
          margin-right: 0;
        }

        .icon{
          @include r;
          height: getVW(59);
          margin-bottom: getVW(8);
          background-repeat: no-repeat;
          background-position: center center;

          &.i0{ background-image: url(../../../assets/icon_about0.png); }
          &.i1{ background-image: url(../../../assets/icon_about1.png); }
          &.i2{ background-image: url(../../../assets/icon_about2.png); }
          &.i3{ background-image: url(../../../assets/icon_about3.png); }
        }

        h1{
          @include r;
          @include fontvw(19,19,#5c5c5c);
          box-sizing: border-box;
          padding: 0 getVW(50);
          padding-bottom: getVW(10);
          margin-bottom: getVW(30);
          text-align: center;

          &:after{
            content: "";
            @include alb(100%,1px);
            width: calc(100% - #{getVW(100)});
            left: getVW(50);
            background-color: #a0a0a0;
            z-index: 1;
          }
        }

        .des{
          @include r;
          @include fontvw(14,24,#5c5c5c);
          min-height: getVW(100);
          text-align: center;

          .number{
            @include fontvw(33,33,#006f37,bolder);
            font-family: 'Arial';
            display: inline-block;
            vertical-align: top;
            margin: 0 getVW(10);
          }
        }

        .photo{
          @include r;

          img{
            width: 100%;
            height: auto;
          }
        }
      }
    }
  }
}

//mobile
$vpw:768;
$vph:1200;
@media screen and (max-width: 1199px) {
.about{
  @include r;
  box-sizing: border-box;
  padding: getVW(60);

  .left-side{
    @include r;
    left: getVW(0);
    top: getVW(0);
