      margin-bottom: getVW(20);
    }

    .vd{
      @include rvw(100,364);
      width: 100%;
      left: 0%;
      top: 0;
      margin-left: 0;
      margin-bottom: getVW(50);
      
      .cover{
        @include alt;
        z-index: 9;
        pointer-events: none;
        opacity: 0;
        background-image: url(../../../assets/btn_videoplay.png);
        background-repeat: no-repeat;
        background-position: center center;
        cursor: pointer;

        &.on{
          opacity: 1;
          pointer-events: auto;
        }
      }

      #player{
        z-index: 0;
      }
    }

    p{
      @include r;
      @include fontvw(14,24,#5c5c5c);
      text-align: justify;
    }
  }

  .right-side{
    @include r;
    box-sizing: border-box;
    padding-left: getVW(0);
    z-index: 0;

    .features{
      @include r;
      margin-top: getVW(70);

      .feature{
        @include r(49%);
        margin-right: 2%;
        margin-bottom: getVW(55);

        &:nth-child(2n){
          margin-right: 0;
        }

        .icon{
          @include r;
